export default {
  name: 'eChart',
  data() {
    return {
      popupVisible: false
    }
  },
  props: {
    options: {
      type: Object,
      default: {}
    },
    autoResize: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    openEchartModel() {
      this.popupVisible = true;
      setTimeout(function () {
        document.getElementsByClassName('echarts')[1].style.height = 12 + 'rem';
      }, 500);

      document.getElementsByClassName('echart-model')[0].classList.add('screen-orientation');
    }
  }
}




